'use client';

/* 
  TestDetailPage.jsx
  ------------------
  âœ…  Medical diagnostic test detail page
  âœ…  Test booking functionality
  âœ…  Related tests powered by test category
  âœ…  Medical-themed UI and content
  âœ…  Price structure for center visit vs home collection
*/

import { useEffect, useMemo, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import Head from 'next/head';
import {
  CalendarIcon,
  Heart,
  Share2,
  Star,
  TestTube,
  MapPin,
  Clock,
  Phone,
  Home,
  Building,
  Shield,
  Award,
} from 'lucide-react';
import { format } from 'date-fns';

// UI Components
import { Button } from '../../../../components/ui/button';
import { Calendar } from '../../../../components/ui/calendar';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '../../../../components/ui/popover';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '../../../../components/ui/select';
import { Input } from '../../../../components/ui/input';
import { toast } from '../../../../components/ui/use-toast';
import { Badge } from '../../../../components/ui/badge';
import { RadioGroup, RadioGroupItem } from '../../../../components/ui/radio-group';

import DOMPurify from 'dompurify';

import { createOrder } from '../../../../services/createorder';
import {
  getProductBySlug,
  getRelatedProducts,
} from '../../../../services/wordpress';

// -----------------------------------------------------------------------------
// ðŸ”§  Utilities
// -----------------------------------------------------------------------------
const skeletonArray = (len) => Array.from({ length: len });

const formatTimeLabel = (hour) => {
  const period = hour >= 12 ? 'PM' : 'AM';
  const displayHour = hour % 12 || 12;
  return `${displayHour}:00 ${period}`;
};

// Medical time slots (7 AM to 8 PM)
const MEDICAL_HOURS = Array.from({ length: 14 }, (_, i) => 7 + i);

// -----------------------------------------------------------------------------
// ðŸ§©  MAIN COMPONENT
// -----------------------------------------------------------------------------
export default function TestDetailPage() {
  const params = useParams();
  const router = useRouter();
  
  const slug = Array.isArray(params?.slug) ? params.slug[0] : params?.slug;

  // ----------------------  DATA  ---------------------- //
  const [test, setTest] = useState(null);
  const [related, setRelated] = useState([]);
  const [loading, setLoading] = useState(true);
  const [relatedLoading, setRelatedLoading] = useState(true);
  const [error, setError] = useState(null);

  // ----------------------  FORM  ---------------------- //
  const [form, setForm] = useState({
    selectedImage: '',
    date: null,
    time: '',
    patientName: '',
    phoneNumber: '',
    age: '',
    gender: '',
    collectionType: 'center', // 'center' or 'home'
    address: '',
  });

  const isFormValid = useMemo(
    () => form.date && form.time && form.patientName && form.phoneNumber && form.age && form.gender && (form.collectionType === 'center' || form.address),
    [form],
  );

  // Price calculation
  const getTestPrice = useMemo(() => {
    const basePrice = test?.price ? Number(test.price) : 300;
    const homeCollectionFee = 200;
    const minHomeCollection = 500;
    
    if (form.collectionType === 'home') {
      return Math.max(basePrice + homeCollectionFee, minHomeCollection);
    }
    return basePrice;
  }, [test?.price, form.collectionType]);

  // ----------------------  EFFECTS  ---------------------- //
  // 1ï¸âƒ£  Fetch test
  useEffect(() => {
    if (!slug) return;

    (async () => {
      try {
        setLoading(true);
        const testData = await getProductBySlug(slug);
        if (!testData) throw new Error('Test not found');
        setTest(testData);
        setForm((f) => ({
          ...f,
          selectedImage: testData.images?.[0]?.src || '/test-placeholder.jpg',
        }));
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    })();
  }, [slug]);

  // 2ï¸âƒ£  Fetch related tests
  useEffect(() => {
    if (!test?.categories?.length) return;

    (async () => {
      try {
        setRelatedLoading(true);
        const categoryId = test.categories[0].id;
        const data = await getRelatedProducts(categoryId, test.id);
        setRelated(data || []);
      } catch (err) {
        console.error('[Related tests]', err);
      } finally {
        setRelatedLoading(false);
      }
    })();
  }, [test]);

  // ----------------------  HANDLERS  ---------------------- //
  const handleField = (field) => (e) => {
    const value = e?.target?.value ?? e;
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const handleBookNow = async () => {
    if (!isFormValid || !test) {
      toast({
        title: "Error",
        description: "Please fill all required fields",
        variant: "destructive",
      });
      return;
    }

    const testSku = `TEST_${Date.now()}`;
    const orderData = {
      billing: {
        first_name: form.patientName.split(' ')[0] || 'Patient',
        last_name: form.patientName.split(' ').slice(1).join(' ') || 'Name',
        email: 'patient@sachdevadiagnostics.com',
        phone: form.phoneNumber,
        address_1: form.collectionType === 'home' ? form.address : 'Sachdeva Diagnostics Center',
      },
      shipping: { 
        first_name: form.patientName.split(' ')[0] || 'Patient',
        last_name: form.patientName.split(' ').slice(1).join(' ') || 'Name',
        address_1: form.collectionType === 'home' ? form.address : 'Sachdeva Diagnostics Center',
      },
      line_items: [
        {
          sku: testSku,
          name: test.name,
          quantity: 1,
          price: getTestPrice.toString(),
          subtotal: getTestPrice.toString(),
          total: getTestPrice.toString(),
        },
      ],
      currency: 'INR',
      meta_data: [
        { key: 'appointment_date', value: format(form.date, 'yyyy-MM-dd') },
        { key: 'appointment_time', value: form.time },
        { key: 'patient_name', value: form.patientName },
        { key: 'patient_age', value: form.age },
        { key: 'patient_gender', value: form.gender },
        { key: 'phone_number', value: form.phoneNumber },
        { key: 'collection_type', value: form.collectionType },
        { key: 'collection_address', value: form.address || 'Center Visit' },
        { key: 'booking_type', value: 'test_booking' },
        { key: 'test_sku', value: testSku },
        { key: 'test_price', value: getTestPrice.toString() },
        { key: 'test_slug', value: slug },
      ],
      status: 'pending',
      payment_method: 'test_booking',
      payment_method_title: 'Test Booking Request',
      set_paid: false,
      shipping_total: form.collectionType === 'home' ? '200.00' : '0.00',
      fee_lines: form.collectionType === 'home' ? [{
        name: 'Home Collection Fee',
        amount: '200.00',
        total: '200.00',
      }] : [],
    };

    try {
      const order = await createOrder(orderData);
      toast({
        title: "Test Booked Successfully!",
        description: `Your test has been booked! Order ID: ${order.id}`,
      });

      router.push(
        `/booking?test=${encodeURIComponent(test.name)}&date=${format(
          form.date,
          'yyyy-MM-dd',
        )}&time=${form.time}&patient=${encodeURIComponent(form.patientName)}&phone=${
          form.phoneNumber
        }&order_id=${order.id}&collection=${form.collectionType}`
      );
    } catch (err) {
      console.error('[createOrder]', err);
      toast({
        title: "Error",
        description: "Failed to book test",
        variant: "destructive",
      });
    }
  };

  // ---------------------------------------------------------------------------
  // ðŸ–¼  UI STATES
  // ---------------------------------------------------------------------------
  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-green-50">
        <div className="container mx-auto px-4 py-12">
          <div className="animate-pulse space-y-8">
            <div className="grid md:grid-cols-2 gap-8">
              <div className="h-96 bg-gray-200 rounded-2xl"></div>
              <div className="space-y-4">
                <div className="h-8 bg-gray-200 rounded w-3/4"></div>
                <div className="h-4 bg-gray-200 rounded w-1/2"></div>
                <div className="h-20 bg-gray-200 rounded"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-green-50 flex items-center justify-center">
        <div className="text-center p-8 bg-white rounded-2xl shadow-lg">
          <h2 className="text-2xl font-bold text-red-600 mb-4">Test Not Found</h2>
          <p className="text-gray-600 mb-6">{error}</p>
          <Button onClick={() => router.push('/tests')} className="bg-[#194b8c] hover:bg-blue-700">
            Browse All Tests
          </Button>
        </div>
      </div>
    );
  }

  if (!test) return null;

  // ---------------------------------------------------------------------------
  // ðŸŽ¨  PAGE CONTENT
  // ---------------------------------------------------------------------------
  return (
    <>
      <Head>
        <title>{`${test.name} â€“ Sachdeva Diagnostics`}</title>
        <meta
          name="description"
          content={
            test.short_description?.replace(/<[^>]*>/g, '') || test.name
          }
        />
      </Head>

      <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-green-50">
        <div className="container mx-auto px-4 py-12 space-y-16">
          {/* ------------------------------------------------------------------ */}
          {/* ðŸ…°  TEST DETAIL SECTION                                            */}
          {/* ------------------------------------------------------------------ */}
          <div className="grid md:grid-cols-2 gap-8">
            {/* ----------------------------  LEFT / IMAGE  ------------------- */}
            <div className="space-y-4">
              <div className="relative group">
                <img
                  src={form.selectedImage}
                  alt={test.name}
                  className="rounded-2xl w-full object-cover aspect-square shadow-2xl ring-4 ring-white/50 group-hover:ring-white/80 transition-all duration-300"
                  onError={(e) => {
                    e.currentTarget.src = '/test-placeholder.jpg';
                  }}
                />

                <div className="absolute top-4 right-4 flex gap-2">
                  <button className="bg-white/80 backdrop-blur-sm p-2 rounded-full shadow-lg hover:bg-white transition-all duration-200 hover:scale-110">
                    <Heart className="w-5 h-5 text-red-500" />
                  </button>
                  <button className="bg-white/80 backdrop-blur-sm p-2 rounded-full shadow-lg hover:bg-white transition-all duration-200 hover:scale-110">
                    <Share2 className="w-5 h-5 text-[#194b8c]" />
                  </button>
                </div>

                {/* NABL Accredited Badge */}
                <div className="absolute bottom-4 left-4">
                  <div className="bg-green-500 text-white px-3 py-1 rounded-full text-sm font-semibold flex items-center gap-1">
                    <Award className="w-4 h-4" />
                    NABL Accredited
                  </div>
                </div>
              </div>

              {test.images?.length > 1 && (
                <div className="flex gap-3 overflow-x-auto pb-2">
                  {test.images.map((img) => (
                    <img
                      key={img.id}
                      src={img.src}
                      alt={img.alt || 'Test image'}
                      onClick={() => handleField('selectedImage')(img.src)}
                      className={`h-20 w-20 object-cover cursor-pointer rounded-xl shadow-lg flex-shrink-0 ring-2 transition-all duration-200 hover:scale-110 ${
                        form.selectedImage === img.src
                          ? 'ring-[#194b8c] shadow-blue-200'
                          : 'ring-white/50 hover:ring-blue-300'
                      }`}
                    />
                  ))}
                </div>
              )}
            </div>

            {/* ----------------------------  RIGHT / INFO  ------------------- */}
            <div className="bg-white/70 backdrop-blur-sm rounded-2xl p-8 shadow-xl">
              <div className="space-y-6">
                {/* Category badge */}
                {test.categories?.length > 0 && (
                  <Badge className="bg-gradient-to-r from-[#194b8c] to-blue-600 text-white px-4 py-2 rounded-full text-sm font-medium shadow-lg">
                    <TestTube className="w-4 h-4 mr-1" />
                    {test.categories[0].name}
                  </Badge>
                )}

                <h1 className="text-3xl font-bold bg-gradient-to-r from-[#194b8c] to-blue-600 bg-clip-text text-transparent leading-tight">
                  {test.name}
                </h1>

                <div className="flex items-center gap-1">
                  {skeletonArray(5).map((_, i) => (
                    <Star key={i} className="w-5 h-5 fill-yellow-400 text-yellow-400" />
                  ))}
                  <span className="text-sm text-gray-600 ml-2">(4.8/5 â€“ 1000+ tests done)</span>
                </div>

                {test.short_description && (
                  <div
                    className="prose prose-sm text-gray-700 leading-relaxed"
                    dangerouslySetInnerHTML={{
                      __html: DOMPurify.sanitize(test.short_description),
                    }}
                  />
                )}

                {/* Price Display */}
                <div className="bg-gradient-to-r from-[#194b8c] to-blue-600 text-white px-6 py-4 rounded-xl shadow-lg">
                  <div className="flex justify-between items-center">
                    <div>
                      <p className="text-2xl font-bold">
                        â‚¹{getTestPrice.toLocaleString('en-IN')}
                      </p>
                      <p className="text-sm opacity-90">
                        {form.collectionType === 'home' ? 'Home Collection' : 'Center Visit'}
                      </p>
                    </div>
                    <div className="text-right">
                      <Shield className="w-8 h-8 mb-1" />
                      <p className="text-xs">Trusted</p>
                    </div>
                  </div>
                </div>

                {/* ------------------  BOOKING FORM  ----------------------- */}
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-gray-800 flex items-center gap-2">
                    <TestTube className="w-5 h-5 text-[#194b8c]" /> Book Your Test
                  </h3>

                  {/* Patient Name */}
                  <div className="relative">
                    <Input
                      placeholder="Patient Name"
                      value={form.patientName}
                      onChange={handleField('patientName')}
                      required
                      className="h-12 rounded-xl border-2 border-blue-200 focus:border-[#194b8c] shadow-md"
                    />
                  </div>

                  {/* Age and Gender */}
                  <div className="grid grid-cols-2 gap-4">
                    <Input
                      placeholder="Age"
                      value={form.age}
                      onChange={handleField('age')}
                      type="number"
                      required
                      className="h-12 rounded-xl border-2 border-blue-200 focus:border-[#194b8c] shadow-md"
                    />
                    <Select onValueChange={handleField('gender')} value={form.gender}>
                      <SelectTrigger className="h-12 rounded-xl border-2 border-blue-200 focus:border-[#194b8c] shadow-md bg-white text-black">
                        <SelectValue placeholder="Gender" className="text-black" />
                      </SelectTrigger>
                      <SelectContent className="bg-white border border-gray-300 shadow-lg">
                        <SelectItem value="male" className="text-black hover:bg-gray-100">Male</SelectItem>
                        <SelectItem value="female" className="text-black hover:bg-gray-100">Female</SelectItem>
                        <SelectItem value="other" className="text-black hover:bg-gray-100">Other</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>

                  {/* Phone */}
                  <div className="relative">
                    <Phone className="absolute left-3 top-3 w-5 h-5 text-[#194b8c]" />
                    <Input
                      placeholder="Phone Number"
                      value={form.phoneNumber}
                      onChange={handleField('phoneNumber')}
                      type="tel"
                      required
                      className="pl-12 h-12 rounded-xl border-2 border-blue-200 focus:border-[#194b8c] shadow-md"
                    />
                  </div>

                  {/* Collection Type */}
                  <div className="space-y-3">
                    <label className="text-sm font-medium text-gray-700">Collection Type</label>
                    <RadioGroup
                      value={form.collectionType}
                      onValueChange={handleField('collectionType')}
                      className="grid grid-cols-2 gap-4"
                    >
                      <div className="flex items-center space-x-2 p-3 border-2 border-gray-200 rounded-lg hover:border-[#194b8c] transition-colors">
                        <RadioGroupItem value="center" id="center" />
                        <label htmlFor="center" className="flex items-center cursor-pointer flex-1">
                          <Building className="h-4 w-4 text-[#194b8c] mr-2" />
                          <div className="text-sm">
                            <div>Center Visit</div>
                            <div className="text-gray-500">â‚¹{Number(test?.price || 300).toLocaleString('en-IN')}</div>
                          </div>
                        </label>
                      </div>
                      <div className="flex items-center space-x-2 p-3 border-2 border-gray-200 rounded-lg hover:border-[#194b8c] transition-colors">
                        <RadioGroupItem value="home" id="home" />
                        <label htmlFor="home" className="flex items-center cursor-pointer flex-1">
                          <Home className="h-4 w-4 text-[#194b8c] mr-2" />
                          <div className="text-sm">
                            <div>Home Collection</div>
                            <div className="text-gray-500">â‚¹{Math.max((Number(test?.price || 300) + 200), 500).toLocaleString('en-IN')}</div>
                          </div>
                        </label>
                      </div>
                    </RadioGroup>
                  </div>

                  {/* Address (if home collection) */}
                  {form.collectionType === 'home' && (
                    <div className="relative">
                      <MapPin className="absolute left-3 top-3 w-5 h-5 text-[#194b8c]" />
                      <Input
                        placeholder="Complete Address for Sample Collection"
                        value={form.address}
                        onChange={handleField('address')}
                        required
                        className="pl-12 h-12 rounded-xl border-2 border-blue-200 focus:border-[#194b8c] shadow-md"
                      />
                    </div>
                  )}

                  {/* Date */}
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className="w-full justify-start h-12 rounded-xl border-2 border-blue-200 hover:border-[#194b8c] shadow-md bg-white text-black"
                      >
                        <CalendarIcon className="mr-3 h-5 w-5 text-[#194b8c]" />
                        {form.date ? format(form.date, 'PPP') : 'Select appointment date'}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0 bg-white border border-gray-300 shadow-lg rounded-xl">
                      <Calendar
                        mode="single"
                        selected={form.date}
                        onSelect={handleField('date')}
                        disabled={(d) => d < new Date()}
                        initialFocus
                        className="rounded-xl bg-white"
                      />
                    </PopoverContent>
                  </Popover>

                  {/* Time */}
                  <Select onValueChange={handleField('time')} value={form.time}>
                    <SelectTrigger className="h-12 rounded-xl border-2 border-blue-200 focus:border-[#194b8c] shadow-md bg-white text-black">
                      <div className="flex items-center">
                        <Clock className="mr-3 h-5 w-5 text-[#194b8c]" />
                        <SelectValue placeholder="Select preferred time" className="text-black" />
                      </div>
                    </SelectTrigger>
                    <SelectContent className="bg-white border border-gray-300 shadow-lg rounded-xl">
                      {MEDICAL_HOURS.map((hour) => (
                        <SelectItem key={hour} value={`${hour}:00`} className="text-black hover:bg-gray-100">
                          {formatTimeLabel(hour)}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>

                  {/* Book Button */}
                  <Button
                    onClick={handleBookNow}
                    disabled={!isFormValid}
                    className="w-full h-14 text-lg font-semibold rounded-xl shadow-lg bg-gradient-to-r from-[#194b8c] to-blue-600 hover:from-blue-700 hover:to-blue-700 text-white transform hover:scale-105 transition-all duration-200 disabled:hover:scale-100 disabled:opacity-50"
                  >
                    {loading ? (
                      <div className="flex items-center gap-2">
                        <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin" />
                        Processing...
                      </div>
                    ) : (
                      <div className="flex items-center gap-2">
                        <TestTube className="w-5 h-5" /> 
                        Book Test - â‚¹{getTestPrice.toLocaleString('en-IN')}
                      </div>
                    )}
                  </Button>
                </div>

                {/* Trust Indicators */}
                <div className="flex items-center justify-between text-sm text-gray-600 pt-4 border-t border-gray-200">
                  <div className="flex items-center gap-1">
                    <span className="w-2 h-2 bg-green-500 rounded-full" /> NABL Accredited
                  </div>
                  <div className="flex items-center gap-1">
                    <span className="w-2 h-2 bg-[#194b8c] rounded-full" /> Same Day Reports
                  </div>
                  <div className="flex items-center gap-1">
                    <span className="w-2 h-2 bg-green-500 rounded-full" /> 30+ Years Trust
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* ------------------------------------------------------------------ */}
          {/* ðŸ…±  RELATED TESTS                                                  */}
          {/* ------------------------------------------------------------------ */}
          <section>
            <h2 className="text-2xl font-bold mb-6 flex items-center gap-2 text-[#194b8c]">
              <TestTube className="w-6 h-6" /> Related Tests
            </h2>

            {/* Loading skeletons */}
            {relatedLoading && (
              <div className="grid grid-cols-2 md:grid-cols-4 gap-6 animate-pulse">
                {skeletonArray(4).map((_, i) => (
                  <div
                    key={i}
                    className="h-64 w-full bg-gradient-to-r from-blue-200 to-green-200 rounded-xl shadow-lg"
                  />
                ))}
              </div>
            )}

            {/* Empty state */}
            {!relatedLoading && related.length === 0 && (
              <p className="text-gray-600">No related tests found.</p>
            )}

            {/* Grid of related tests */}
            {!relatedLoading && related.length > 0 && (
              <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
                {related.map((rt) => (
                  <div
                    key={rt.id}
                    className="group bg-white/70 backdrop-blur-sm rounded-2xl p-4 shadow-lg hover:shadow-2xl transition-all duration-200"
                  >
                    <img
                      src={rt.images?.[0]?.src || '/test-placeholder.jpg'}
                      alt={rt.name}
                      className="aspect-square object-cover rounded-xl w-full mb-3"
                    />
                    <h3 className="text-base font-semibold line-clamp-2 mb-1">{rt.name}</h3>
                    <p className="text-sm text-gray-600 mb-3">
                      â‚¹{Number(rt.price).toLocaleString('en-IN')}
                    </p>
                    <Button
                      onClick={() => router.push(`/tests/${rt.slug}`)}
                      className="w-full bg-gradient-to-r from-[#194b8c] to-blue-600 text-white rounded-xl hover:from-blue-700 hover:to-blue-700"
                    >
                      View Test Details
                    </Button>
                  </div>
                ))}
              </div>
            )}
          </section>
        </div>
      </div>

      {/* Global styles for dropdown visibility */}
      <style jsx global>{`
        [data-radix-select-content] {
          background-color: white !important;
          color: black !important;
          border: 1px solid #d1d5db !important;
          border-radius: 8px !important;
          box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1) !important;
          z-index: 50 !important;
        }
        
        [data-radix-select-item] {
          color: black !important;
          background-color: white !important;
          padding: 8px 12px !important;
          cursor: pointer !important;
        }
        
        [data-radix-select-item]:hover,
        [data-radix-select-item][data-highlighted] {
          background-color: #f3f4f6 !important;
          color: #194b8c !important;
        }
        
        [data-radix-popover-content] {
          background-color: white !important;
          color: black !important;
          border: 1px solid #d1d5db !important;
          border-radius: 8px !important;
          box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1) !important;
          z-index: 50 !important;
        }
      `}</style>
    </>
  );
}
